<script>
  import { AppForms } from "../../libraries/AppForms";

  import { TextEditorStore } from "./TextEditor.store";

  let { selectedColor } = $TextEditorStore;

  $: {
    if ($TextEditorStore) {
      selectedColor = selectedColor;
    }
  }

  export let callUpdate = () => {};
  export let updatePosition = () => {};

  let formFilter;

  let showInputColor = "RGB";

  function getDataForm(e, formFilter, type) {
    let toUpdate = AppForms.retrieve({
      form: formFilter,
      targets: ".formElements",
    });

    switch (type) {
      case "hex":
        callUpdate(e.target.value, "hex", selectedColor);
        break;
      case "rgb":
        callUpdate(
          [toUpdate.red, toUpdate.green, toUpdate.blue],
          "rgb",
          selectedColor
        );
        break;
      case "hsl":
        callUpdate(
          [toUpdate.hue, toUpdate.saturation, toUpdate.lightness],
          "hsl",
          selectedColor
        );
        break;
      case "hsv":
        callUpdate(
          [toUpdate.hue, toUpdate.saturation, toUpdate.value],
          "hsv",
          selectedColor
        );
        break;
      case "cmyk":
        callUpdate(
          [toUpdate.cyan, toUpdate.magenta, toUpdate.yellow, toUpdate.key],
          "cmyk",
          selectedColor
        );
        break;
      default:
        break;
    }

    e.target.focus();

    selectedColor = selectedColor;
    window.rgb = selectedColor.hsv.hue;

    let circle = document.querySelector(".circle");

    let max = 200 - 15.84;

    let x = Math.round((max * selectedColor.hsv.saturation) / 100);
    let y = Math.round((selectedColor.hsv.value * max) / 100);

    updatePosition(x, y, circle);

    window.colorCodeStatus = true;
    window.colorCodeIsActive = true;
  }

  function showColorFormat(e) {
    showInputColor = e.target.textContent;
  }
</script>

<form
  class="main-container"
  action=""
  bind:this={formFilter}
  on:keydown={(e) => {
    if (e.key === "Enter" || e.key === "Click") {
      getDataForm(e, formFilter);
      e.preventDefault();
    }
  }}
  on:submit|preventDefault={getDataForm(formFilter)}
>
  <div class="rgba-code-container">
    {#each showInputColor as item}
      <div class="rgba-item">
        <p>{item}</p>
        <input
          type="number"
          onKeyPress="if(this.value.length==3) return false;"
          on:input={(e) =>
            getDataForm(
              e,
              formFilter,
              showInputColor === "HSL"
                ? "hsl"
                : showInputColor === "HSV"
                ? "hsv"
                : showInputColor === "RGB"
                ? "rgb"
                : "cmyk"
            )}
          class="rgba-item-number formElements"
          contenteditable="true"
          name={showInputColor === "HSL" ||
          (showInputColor === "HSV" && item === "H")
            ? "hue"
            : showInputColor === "HSL" ||
              (showInputColor === "HSV" && item === "S")
            ? "saturation"
            : showInputColor === "HSL" && item === "L"
            ? "lightness"
            : showInputColor === "HSV" && item === "V"
            ? "value"
            : showInputColor === "RGB" && item === "R"
            ? "red"
            : showInputColor === "RGB" && item === "G"
            ? "green"
            : showInputColor === "RGB" && item === "B"
            ? "blue"
            : showInputColor === "CMYK" && item === "C"
            ? "cyan"
            : showInputColor === "CMYK" && item === "M"
            ? "magenta"
            : showInputColor === "CMYK" && item === "Y"
            ? "yellow"
            : "key"}
          value={showInputColor === "HSL" && item === "H"
            ? selectedColor.hsl.hue
            : showInputColor === "HSL" && item === "S"
            ? selectedColor.hsl.saturation
            : showInputColor === "HSL" && item === "L"
            ? selectedColor.hsl.lightness
            : showInputColor === "HSV" && item === "H"
            ? selectedColor.hsv.hue
            : showInputColor === "HSV" && item === "S"
            ? selectedColor.hsv.saturation
            : showInputColor === "HSV" && item === "V"
            ? selectedColor.hsv.value
            : showInputColor === "RGB" && item === "R"
            ? selectedColor.rgb.red
            : showInputColor === "RGB" && item === "G"
            ? selectedColor.rgb.green
            : showInputColor === "RGB" && item === "B"
            ? selectedColor.rgb.blue
            : showInputColor === "CMYK" && item === "C"
            ? selectedColor.cmyk.cyan
            : showInputColor === "CMYK" && item === "M"
            ? selectedColor.cmyk.magenta
            : showInputColor === "CMYK" && item === "Y"
            ? selectedColor.cmyk.yellow
            : selectedColor.cmyk.key}
        />
      </div>
    {/each}
  </div>
  <div class="hex-hsl">
    <p>HEX</p>
    <div class="hex-hsl-codes">
      <p>#</p>
      <input
        class="hex-input"
        type="text"
        maxlength="6"
        contenteditable="true"
        on:input={(e) => getDataForm(e, formFilter, "hex")}
        value={selectedColor.hex}
      />
    </div>
  </div>
</form>

<div class="a-links">
  <button on:click={(e) => showColorFormat(e)}>RGB</button>
  <button on:click={(e) => showColorFormat(e)}>HSL</button>
  <button on:click={(e) => showColorFormat(e)}>HSV</button>
  <button on:click={(e) => showColorFormat(e)}>CMYK</button>
</div>

<style>
  .main-container {
    display: flex;
    flex-direction: row;
    align-items: flex-end;
    width: 100%;
  }
  .rgba-code-container {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
    height: 48px;
    width: 70%;
  }
  .rgba-item {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    width: 20%;
    height: 100%;
  }
  .rgba-item p {
    height: 16px;
    width: 100%;
    text-align: center;
    font-size: 12px;
  }
  .rgba-item-number {
    display: flex;
    align-items: center;
    text-align: center;
    justify-content: center;
    width: 100%;
    height: 32px;
    border: 1px solid #e1e8ed;
    border-radius: 20px;
    font-size: 12px;
  }
  .hex-hsl {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 70px;
  }
  .hex-hsl P {
    height: 16px;
    width: 100%;
    text-align: center;
    font-size: 12px;
  }
  .hex-hsl-codes {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 32px;
    border: 1px solid #e1e8ed;
    border-radius: 20px;
    font-size: 12px;
    padding: 0 0.55rem 0 0.55rem;
  }
  input[type="number"]::-webkit-inner-spin-button {
    -webkit-appearance: none;
  }

  .hex-input {
    font-size: 12px;
    border: none;
    outline: none;
    max-width: 50px;
    color: #415b76;
  }

  .a-links {
    width: 100%;
    display: flex;
    justify-content: space-evenly;
  }

  .a-links button {
    color: #415b76;
    cursor: pointer;
    text-decoration: underline;
    font-size: 14px;
    border: none;
    outline: none;
    background-color: transparent;
  }
</style>
