<script>
import TextEditorColorPicker from './components/TextEditor/TextEditorColorPicker.svelte';

class ColorPicker
{
    constructor(node)
    {
        let app = this;
        this.color = "";
        
        new TextEditorColorPicker({
            target: node,
            props: {
                on_change: ( color )=>{
                    app.color = color;
                    app.change();
                }
            }
        })
    }
    change()
    {
        console.log(this.color);
    }
    open(){

    }
    close(){}
}

window.ColorPicker = ColorPicker;

</script>