import { writable } from "svelte/store";

function Controller() {
  const { subscribe, update, set } = writable({
    selectedColor: {
      hsv: {
        hue: "0",
        saturation: "100",
        value: "100",
      },
      rgb: {
        red: "255",
        green: "0",
        blue: "0",
      },
      hsl: {
        hue: "0",
        saturation: "100",
        lightness: "50",
      },
      cmyk: {
        cyan: "0",
        magenta: "100",
        yellow: "100",
        key: "0",
      },
      hex: "ff0000",
      alpha: "1",
    },
  });

  const controller = {
    subscribe,
    update: (field, content) => {
      update((data) => {
        data[field] = content;
        return data;
      });
    },
  };

  return controller;
}

export const TextEditorStore = Controller();
