<script>
  import MainCanvasColor from "./MainCanvasColor.svelte";
  import ColorCode from "./ColorCode.svelte";
  import CaptureColor from "./CaptureColor.svelte";
  import Styles from "./../../Styles.svelte";
  import ColorConverter from "./converter.js";

  import { TextEditorStore } from "./TextEditor.store.js";
  import { TextEditorCSSStore } from "./TextEditorCSS.store.js";
  import { random_string } from "../../libraries/random_string";

  let { selectedColor } = $TextEditorStore;
  let { itemSelected } = $TextEditorCSSStore;

  export let img = "";
  export let emitter;
  export let on_change = $$props.on_change ?? function(){};

  let Converter = new ColorConverter();

  let circlePosition = {
    x: 184,
    y: 0,
  };

  function updatePosition(x, y, circle) {
    y = y - 184;

    if (y < 0) {
      y = -1 * y;
    }

    circlePosition.x = x;
    circlePosition.y = y;

    circle.style.left = x;
    circle.style.top = y;
  }

  let random = random_string(5);

  function getElementToEdit(rgbaColor) {
    on_change(rgbaColor);
    return;
    let selected = document.getSelection();
    let element = selected.anchorNode?.parentNode;

    let data = {
      id: element.id,
      styles: [rgbaColor],
    };

    TextEditorCSSStore.update("dataStyles", data);

    let { dataStyles } = $TextEditorCSSStore;

    /* console.log(dataStyles); */

    if (img === "icon-tripod-text-color") {
      document.execCommand("foreColor", false, rgbaColor);
    } else {
      document.execCommand("backColor", false, rgbaColor);
    }
  }

  function callUpdate(arrayData, type, toUpdateColor) {
    selectedColor = Converter.updatedColor(arrayData, type, toUpdateColor);

    let rgbaColor = `rgba(${selectedColor.rgb.red},${selectedColor.rgb.green},${selectedColor.rgb.blue},${selectedColor.alpha})`;

    TextEditorStore.update("selectedColor", selectedColor);

    
    if (img === "icon-tripod-text-color") {
      
      emitter.command("color", {
        color: rgbaColor,
        isColor: true,
        isBgColor: false,
      });
    } else if (img === "icon-tripod-background-color") {

      emitter.command("color", {
        color: rgbaColor,
        isBgColor: true,
        isColor: false,
      });
    } else {
      getElementToEdit(rgbaColor);
    }
  }
</script>

<div class="picker-container has-shadow">
  <div class="title">Selector de color</div>
  <div class="canvas-container">
    <MainCanvasColor {callUpdate} {circlePosition} />
    <div class="capture-color-container">
      <CaptureColor {callUpdate} {updatePosition} />
    </div>
  </div>
  <div class="color-code-container">
    <ColorCode {callUpdate} {updatePosition} />
  </div>
  <div class="default-colors-container" />
</div>

<style>
  .picker-container {
    width: 268px;
    min-height: 422px;
    background-color: #ffffff;
    opacity: 1;
    border-radius: 22px;
    padding: 20px;
    margin: auto;
    user-select: none;
  }

  .picker-container .title {
    color: #415b76;
    font-size: 18px;
    text-align: center;
    opacity: 1;
  }

  .canvas-container {
    width: 228px;
    height: 232px;
    display: flex;
    flex-direction: row;
    position: relative;
  }

  .capture-color-container {
    position: absolute;
    bottom: 0;
    right: 0;
    width: 20px;
    height: 20px;
  }

  .color-code-container {
    margin-top: 16px;
    width: 228px;
    height: 100px;
  }

  .default-colors-container {
    margin-top: 16px;
    width: 228px;
    height: 40px;
  }
</style>
