class ColorConverter {
	constructor(values, type) {
		this.values = values;
		this.type = type;

		// RGB -> HSL
		//hex -> rgb -> hsl
		//rgb -> hsv / rgb -> hex
		//hsv -> rgb -> hex
	}

	HSBToRGB(hue, saturation, value) {
		let h = parseInt(hue), s = parseInt(saturation), b= parseInt(value);
		s /= 100;
		b /= 100;
		const k = (n) => (n + h / 60) % 6;
		const f = (n) => b * (1 - s * Math.max(0, Math.min(k(n), 4 - k(n), 1)));

		let red = 255 * f(5);
		let green = 255 * f(3);
		let blue = 255 * f(1);

		red = Math.round(red.toString());
		green = Math.round(green.toString());
		blue = Math.round(blue.toString());

		return {
			red: red,
			green: green,
			blue: blue
		}
	}

	HEXToRGB(hex) {
		let arrayRgb;
		if(hex[0] === "#"){
		arrayRgb = hex.slice(1).match(/.{1,2}/g);
		}else{
		arrayRgb = hex.match(/.{1,2}/g);
		}
		let rgba2 = [`${parseInt(arrayRgb[0], 16)}`, `${parseInt(arrayRgb[1], 16)}`, `${parseInt(arrayRgb[2], 16)}`];
		return {
		red: rgba2[0],
		green: rgba2[1],
		blue: rgba2[2]
		}
	}

	RGBToHSB(red, green, blue) {
		let r = parseInt(red);
		let g = parseInt(green);
		let b = parseInt(blue);
		let hu, sat, val;
		r /= 255;
		g /= 255;
		b /= 255;
		const v = Math.max(r, g, b),
		n = v - Math.min(r, g, b);
		const h =
		n === 0
		? 0
		: n && v === r
		? (g - b) / n
		: v === g
		? 2 + (b - r) / n
		: 4 + (r - g) / n;

		hu = Math.round(60 * (h < 0 ? h + 6 : h));
		sat = Math.round(v && (n / v) * 100);
		val = Math.round(v * 100);
		return {
			hue: hu,
			saturation: sat,
			value: val
		}
	}

	RGBToHSL(red, green, blue) {
		let r = parseInt(red);
		let g = parseInt(green);
		let b = parseInt(blue);
		let hu, sat, val;
		r /= 255;
		g /= 255;
		b /= 255;
		const v = Math.max(r, g, b),
		n = v - Math.min(r, g, b);
		const h =
		n === 0
		? 0
		: n && v === r
		? (g - b) / n
		: v === g
		? 2 + (b - r) / n
		: 4 + (r - g) / n;

		hu = (h < 0 ? h + 6 : h);
		sat = v && (n / v);
		val = v;

		function hsv2hsl(a,b,c){return[a,b*c/((a=(2-b)*c)<1?a:2-a) * 100,a/2 * 100]}
		let data = hsv2hsl(hu, sat, val);

		for (const item in data) {
			if(isNaN(data[item])){
				data[item] = 0
			}
		}

		hu = Math.round(60 * (h < 0 ? h + 6 : h));
		sat = Math.round(data[1]);
		val = Math.round(data[2]);
		
		return {
			hue: hu,
			saturation: sat,
			lightness: val
		}
	}

	RGBToMCYK(red, green, blue) {
		let cyan, magenta, yellow, key;

		cyan = 1 - (parseInt(red) / 255);
		magenta = 1 - (parseInt(green) / 255);
		yellow = 1 - (parseInt(blue) / 255);
		key = Math.min(cyan, magenta,yellow);
		if(key === 1){
			return [0,0,0,key * 100];
		}
		cyan = Math.round((cyan - key) / (1 - key) * 100);
		magenta = Math.round((magenta - key) / (1 - key) * 100);
		yellow = Math.round((yellow - key) / (1 - key) * 100);
		key = Math.round(key * 100);

		return {
			cyan: cyan,
			magenta: magenta,
			yellow: yellow,
			key: key
		};
	}

	MCYKToRGB(cyan, magenta, yellow, key) {
		cyan = parseInt(cyan);
		magenta = parseInt(magenta);
		yellow = parseInt(yellow);
		key = parseInt(key);

		key = (key / 100);
		cyan = (cyan / 100) * (1 - key) + key;
		magenta = (magenta / 100) * (1 - key) + key;
		yellow = (yellow / 100) * (1 - key) + key;
		
		let red = 1 - cyan, green = 1 - magenta, blue = 1 - yellow;

		red = Math.round(255 * red);
		green = Math.round(255 * green);
		blue = Math.round(255 * blue);

		return {
			red: red,
			green: green,
			blue: blue
		}
	}

	RGBToHex(red, green, blue) {
		let r = parseInt(red), g = parseInt(green), b = parseInt(blue);
		return ((r << 16) + (g << 8) + b).toString(16).padStart(6, "0");
	}

	HSLToHSV(hslH, hslS, hslL){
		hslH = parseInt(hslH);
		hslS = parseInt(hslS);
		hslL = parseInt(hslL);
		const hsv1 = hslS * (hslL < 50 ? hslL : 100 - hslL) / 100;
		const hsvS = hsv1 === 0 ? 0 : 2 * hsv1 / (hslL + hsv1) * 100;
		const hsvV = hslL + hsv1;
		return {
			hue: hslH,
			saturation: hsvS,
			value: hsvV
		}
	}

	alpha() {
		console.log("HOLA");
	}

	updatedColor(values, type, selectedColor){

        switch (type) {
            case "hsv":
                for (const pos in values) {
                    if (values[pos] === '') {
                        values[pos] = 0;
                    }
                }
                if(values[1] === 0 && values[2] === 0){
                    selectedColor.hsv.hue = values[0];
                }else{
                    selectedColor.hsv.hue = values[0];
                    selectedColor.hsv.saturation = values[1]
                    selectedColor.hsv.value = values[2]
                }
                selectedColor.rgb = this.HSBToRGB(selectedColor.hsv.hue, selectedColor.hsv.saturation, selectedColor.hsv.value);
                selectedColor.hex = this.RGBToHex(selectedColor.rgb.red, selectedColor.rgb.green, selectedColor.rgb.blue);
                selectedColor.hsl = this.RGBToHSL(selectedColor.rgb.red, selectedColor.rgb.green, selectedColor.rgb.blue);
				selectedColor.cmyk = this.RGBToMCYK(selectedColor.rgb.red, selectedColor.rgb.green, selectedColor.rgb.blue);
            break;

			case "hsl":
                for (const pos in values) {
                    if (values[pos] === '') {
                        values[pos] = 0;
                    }
                }
                if(values[1] === 0 && values[2] === 0){
                    selectedColor.hsl.hue = values[0];
                }else{
                    selectedColor.hsl.hue = values[0];
                    selectedColor.hsl.saturation = values[1]
                    selectedColor.hsl.lightness = values[2]
                }
				selectedColor.hsv = this.HSLToHSV(selectedColor.hsl.hue, selectedColor.hsl.saturation, selectedColor.hsl.lightness);
				console.log(selectedColor.hsv);
                selectedColor.rgb = this.HSBToRGB(selectedColor.hsv.hue, selectedColor.hsv.saturation, selectedColor.hsv.value);
                selectedColor.hex = this.RGBToHex(selectedColor.rgb.red, selectedColor.rgb.green, selectedColor.rgb.blue);
				selectedColor.cmyk = this.RGBToMCYK(selectedColor.rgb.red, selectedColor.rgb.green, selectedColor.rgb.blue);
            break;

			case "cmyk":
                for (const pos in values) {
                    if (values[pos] === '') {
                        values[pos] = 0;
                    }
                }
				selectedColor.cmyk.cyan = values[0];
				selectedColor.cmyk.magenta = values[1];
				selectedColor.cmyk.yellow = values[2];
				selectedColor.cmyk.key = values[3];
				selectedColor.rgb = this.MCYKToRGB(selectedColor.cmyk.cyan, selectedColor.cmyk.magenta, selectedColor.cmyk.yellow, selectedColor.cmyk.key);
				selectedColor.hsv = this.RGBToHSB(selectedColor.rgb.red, selectedColor.rgb.green, selectedColor.rgb.blue);
				selectedColor.hsl = this.RGBToHSL(selectedColor.rgb.red, selectedColor.rgb.green, selectedColor.rgb.blue);
				selectedColor.hex = this.RGBToHex(selectedColor.rgb.red, selectedColor.rgb.green, selectedColor.rgb.blue);
            break;

            case "rgb":
                for (const pos in values) {
                    if (values[pos] === '') {
                        values[pos] = 0;
                    }
                }
                selectedColor.rgb.red = values[0];
                selectedColor.rgb.green = values[1];
                selectedColor.rgb.blue = values[2];
                selectedColor.hex = this.RGBToHex(selectedColor.rgb.red, selectedColor.rgb.green, selectedColor.rgb.blue);
                selectedColor.hsv = this.RGBToHSB(selectedColor.rgb.red, selectedColor.rgb.green, selectedColor.rgb.blue);
                selectedColor.hsl = this.RGBToHSL(selectedColor.rgb.red, selectedColor.rgb.green, selectedColor.rgb.blue);
				selectedColor.cmyk = this.RGBToMCYK(selectedColor.rgb.red, selectedColor.rgb.green, selectedColor.rgb.blue);
            break;

            case "hex":
                if (values[0] === "#") {
                    selectedColor.hex = values.slice(1);
                }else{
                    selectedColor.hex = values;
                }
                selectedColor.rgb = this.HEXToRGB(values);
                selectedColor.hsv = this.RGBToHSB(selectedColor.rgb.red, selectedColor.rgb.green, selectedColor.rgb.blue);
                selectedColor.hsl = this.RGBToHSL(selectedColor.rgb.red, selectedColor.rgb.green, selectedColor.rgb.blue);
				selectedColor.cmyk = this.RGBToMCYK(selectedColor.rgb.red, selectedColor.rgb.green, selectedColor.rgb.blue);
            break;

            case "alpha":
                selectedColor.alpha = values / 10;
            break;
        }
		return selectedColor;
    }
}

export default ColorConverter;
