import { writable } from "svelte/store";

function Controller() {
  const { subscribe, update, set } = writable({
    dataStyles: {
      "test": {
        id: "test",
        styles: [
          "background-color: rgba(0,200,0,1);"
        ]
      },
      "gfd": {
        id: "gfd",
        styles: [
          "background-color: rgba(0,200,0,1);"
        ]
      },

    }
  });

  const controller = {
    subscribe,
    update: (field, content) => {
      update((data) => {
        /**
         * verificar si el id existe
         * si existe, agregar estilos
         * si no existe, crear un json con los campos id y stilos
         */

        if (data[field][content.id]) {
          /**
           * cuando el campo existe debemos verificar si el estilo existe
           * si el estilo existe: lo eliminamos.
           * si el estilo no existe: lo agregamos.
           * adStyles: almacena los estilos que debemos agregar despues de filtrarlos.
           */
          for (let val of data[field][content.id].styles) {
            if (val != content.styles[0]) {
              data[field][content.id].styles = [...data[field][content.id].styles, content.styles]
            } else {
              data[field][content.id].styles.splice(data[field][content.id].styles.indexOf(content.styles[0], 1))
            }
          }

          data[field][content.id] = {id : content.id, styles : content.styles};
        } else {
          /**
           * cuando el campo no exixste debemos crearlo
           */
          data[field][content.id] = {id : content.id, styles : content.styles};
        }
/*         console.log(data[field][content.id]); */
        return data;
      });
    },
  };

  return controller;
}

export const TextEditorCSSStore = Controller();
