// eslint-disable-next-line import/no-unresolved

import '@interactjs/auto-start'
import '@interactjs/actions/drag'
import '@interactjs/actions/resize'
import '@interactjs/modifiers'
import interact from '@interactjs/interact'


const DraggablePicker = ({
  container
}, callback)=>{
  callback = callback || function(){};
  
  const width = 15.84;
  const height = width; // to perform a square
  const canvasSize = 200; // square
  const max = canvasSize - width;
  interact(container).draggable({
    inertia: true,
    listeners: {
      move (event) {
        let position = { x: +(container.dataset.x), y: +(container.dataset.y) }

        position.x += event.dx
        position.y += event.dy

        event.target.style.left = `${position.x}px`;
        event.target.style.top = `${position.y}px`;

        let X = position.x;
        let Y = position.y;
        event.target.dataset.x = X;
        event.target.dataset.y = Y;

        
        let saturation = Math.round((X*100)/max);
        let value = Math.abs( Math.round((Y*100)/max) - 100);

        event.target.dataset.saturation = saturation;
        event.target.dataset.value = value;
        
        callback(saturation, value);
      },
    },
    modifiers: [
      interact.modifiers.restrictRect({
        restriction: 'parent',
      })
    ],
  })
}

export {DraggablePicker}