<script>
  import { TextEditorStore } from "./TextEditor.store";

  let { selectedColor } = $TextEditorStore;

  $: {
    if ($TextEditorStore) {
      selectedColor = selectedColor;
    }
  }

  export let callUpdate = () => {};
  export let updatePosition = () => {};

  /* Función que ejecuta el eyedropper */
  document.addEventListener("click", (e) => {
    if (e.target.matches("#eye-dropper") ||e.target.matches("#eye-dropper *")) {
      if (!window.EyeDropper) {
        console.log("Your browser does not support the EyeDropper API");
        return;
      }

      const eyeDropper = new EyeDropper();

      eyeDropper
        .open()
        .then((result) => {
          toRgb(result.sRGBHex);
        })
        .catch((e) => {
          console.log(e);
        });
    }
  });

  /* Función que obtiene el hex del rgb */
  function toRgb(hex) {
    callUpdate(hex, "hex", selectedColor);

    let circle = document.querySelector(".circle");

    let max = 200 - 15.84;

    let x = Math.round((max * selectedColor.hsv.saturation) / 100);
    let y = Math.round((selectedColor.hsv.value * max) / 100);

    updatePosition(x, y, circle);

    window.rgb = selectedColor.hsv.hue;
    window.captureColorStatus = true;
  }
</script>

<div id="eye-dropper">
  <img
    class="img"
    src="https://ywzxemluhxrzjvojqjld.supabase.in/storage/v1/object/sign/tripobucket/3Pod_Plataform_Icon_Cuentas-gotas.svg?token=eyJhbGciOiJIUzI1NiIsInR5cCI6IkpXVCJ9.eyJ1cmwiOiJ0cmlwb2J1Y2tldC8zUG9kX1BsYXRhZm9ybV9JY29uX0N1ZW50YXMtZ290YXMuc3ZnIiwiaWF0IjoxNjQ2MjU3ODQzLCJleHAiOjE5NjE2MTc4NDN9.dZEI9YwkgnSl3NdMIXY0bVg_iqHRRb05IlHsFUJPURA"
    alt="text-color-icon"
  />
</div>

<style>
  #eye-dropper {
    position: relative;
    width: 100%;
    height: 100%;
    align-items: center;
    cursor: pointer;
    border-radius: 5px;
  }
</style>
