<script>
  import { TextEditorStore } from "./TextEditor.store.js";
  import { onMount, onDestroy } from "svelte";
  import { DraggablePicker } from "../../libraries/Draggable/DraggablePicker";
  import { random_string } from "../../libraries/random_string";
  import ColorConverter from "./converter.js";

  let { selectedColor } = $TextEditorStore;

  const Converter = new ColorConverter();

  export let callUpdate = () => {};
  export let circlePosition = {};

  $: {
    if ($TextEditorStore) {
      selectedColor = selectedColor;
    }
  }

  let rangeValue = 0;
  let rangeAlphaValue = 100;
  let alpha = selectedColor.alpha;
  let canvasBlock;
  let hexRangeColor = "#" + selectedColor.hex;

  let isClicked = false;
  let test = false;

  let canvas_id = random_string(5);

  let colorBlock;
  let ctx;
  let rgbColor;
  window.colorCodeIsActive = false;

  function canvasRender() {
    rgbColor = `rgb(${selectedColor.rgb?.red}, ${selectedColor.rgb?.green}, ${selectedColor.rgb?.blue})`;
    colorBlock = document.getElementById(`${canvas_id}`);
    ctx = colorBlock.getContext("2d");
    let width = colorBlock.width;
    let height = colorBlock.height;
    let alphaRange = document.querySelector(".alphaRange");
    let x = 0;
    let y = 0;
    ctx.rect(0, 0, width, height);
    fillGradient();

    ctx.rect(0, 0, width, height);

    ctx.fillRect(0, 0, 300, 300);

    /* console.log(rgbColor); */

    fillGradient();

    //Esta funcion pinta el canvas
    function fillGradient() {
      ctx.fillStyle = rgbColor;

      ctx.fillRect(0, 0, width, height);

      let grdWhite = ctx.createLinearGradient(0, 0, width, 0);
      grdWhite.addColorStop(0, "rgba(255,255,255,1)");
      grdWhite.addColorStop(1, "rgba(255,255,255,0)");
      ctx.fillStyle = grdWhite;
      ctx.fillRect(0, 0, width, height);

      let grdBlack = ctx.createLinearGradient(0, 0, 0, height);
      grdBlack.addColorStop(0, "rgba(0,0,0,0)");
      grdBlack.addColorStop(1, "rgba(0,0,0,1)");
      ctx.fillStyle = grdBlack;
      ctx.fillRect(0, 0, width, height);
    }

    //Funcion que captura la posicion del cursor sobre el canvas para asignarselo al circulo
    function draw(e) {
      let circle = color_selector;
      x = e.offsetX;
      y = e.offsetY;

      circle.style.left = x - 8 + "px";
      circle.style.top = y - 8 + "px";

      circle.dataset.x = x - 8;
      circle.dataset.y = y - 8;

      let imageData = ctx.getImageData(x, y, 1, 1).data;
      selectedColor.rgb = {
        red: imageData[0],
        green: imageData[1],
        blue: imageData[2],
      };

      callUpdate(
        [
          selectedColor.rgb.red,
          selectedColor.rgb.green,
          selectedColor.rgb.blue,
        ],
        "rgb",
        selectedColor
      );
      hexRangeColor = "#" + selectedColor.hex;
    }

    //Funcion que actualiza el input range de acuerdo a la posicion en la que se encuentre
    function inputAlphaRange() {
      alpha = rangeAlphaValue / 10;

      callUpdate(alpha, "alpha", selectedColor);

      /*       console.log($TextEditorStore.selectedColor); */
    }

    alphaRange.addEventListener("input", inputAlphaRange, false);
    canvasBlock.addEventListener("click", draw);
  }

  function restartColors() {
    return (selectedColor = {
      hsv: {
        hue: "0",
        saturation: "100",
        value: "100",
      },
      rgb: {
        red: "255",
        green: "0",
        blue: "0",
      },
      hsl: {
        hue: "0",
        saturation: "100",
        lightness: "50",
      },
      cmyk: {
        cyan: "0",
        magenta: "100",
        yellow: "100",
        key: "0",
      },
      hex: "ff0000",
      alpha: "1",
    });
  }

  onMount(() => {
    rgbColor = `rgb(255,0,0)`;
    canvasRender();
  });

  onDestroy(() => {
    selectedColor = restartColors();

    TextEditorStore.update("selectedColor", selectedColor);
  });

  let hsv_color = [selectedColor.hsv.hue, 0, 0];
  let color_selector;
  onMount(() => {
    DraggablePicker({ container: color_selector }, (S, V) => {
      if (hsv_color[0] === 0) {
        hexRangeColor = "#" + selectedColor.hex;
      }
      hsv_color[1] = S;
      hsv_color[2] = V;
      callUpdate(hsv_color, "hsv", selectedColor);
      hexRangeColor = "#" + selectedColor.hex;
      window.colorCodeIsActive = false;
    });
  });

  let hue_colors = [];
  for (let index = 0; index < 359; index++) {
    let percent = (index * 100) / 359;
    hue_colors.push(`hsl(${index},100%,50%) ${percent.toFixed(2)}%`);
  }
  hue_colors = `background: linear-gradient(90deg, ${hue_colors.join(", ")});`;

  function updatehex() {
    hexRangeColor = "#" + selectedColor.hex;
  }

  function getAlphaPosition(e) {
    rangeAlphaValue = e.target.value;
  }

  document.addEventListener("mousemove", (e) => {
    if (e.target.matches(".range")) {
      if (test) {
        isClicked = true;
      }
    }
  });

  document.addEventListener("mousedown", (e) => {
    if (e.target.matches(".range")) {
      isClicked = true;
      test = true;
    }
  });

  document.addEventListener("click", (e) => {
    if (e.target.matches(".range")) {
      isClicked = true;
    }
  });

  document.addEventListener("touchstart", (e) => {
    if (e.target.matches(".range")) {
      isClicked = true;
    }
  });

  document.addEventListener("touchmove", (e) => {
    if (e.target.matches(".range")) {
      isClicked = true;
    }
  });

  document.addEventListener("mouseup", (e) => {
    if (e.target.matches(".range")) {
      test = false;
    }
  });

  $: {
    if (rangeValue) {
      if (isClicked) {
        isClicked = false;
        hsv_color[0] = rangeValue - 1;
        getColorFromRange();

        callUpdate(hsv_color, "hsv", selectedColor);

        if (hsv_color[0] != 0) {
          updatehex();
        }
      }
    }
  }
  function updatefrom() {
    hsv_color[0] = window.rgb;
    let rgbC = Converter.HSBToRGB(hsv_color[0], "100", "100");
    hexRangeColor = "#" + Converter.RGBToHex(rgbC.red, rgbC.green, rgbC.blue);
    ctx.fillStyle = `rgb(${rgbC.red}, ${rgbC.green}, ${rgbC.blue})`;
    ctx.fillRect(0, 0, 200, 300);

    let grdWhite = ctx.createLinearGradient(0, 0, 200, 0);
    grdWhite.addColorStop(0, "rgba(255,255,255,1)");
    grdWhite.addColorStop(1, "rgba(255,255,255,0)");
    ctx.fillStyle = grdWhite;
    ctx.fillRect(0, 0, 200, 300);

    let grdBlack = ctx.createLinearGradient(0, 0, 0, 300);
    grdBlack.addColorStop(0, "rgba(0,0,0,0)");
    grdBlack.addColorStop(1, "rgba(0,0,0,1)");
    ctx.fillStyle = grdBlack;
    ctx.fillRect(0, 0, 200, 300);
  }
  $: {
    if (selectedColor) {
      if (window.colorCodeStatus) {
        updatefrom();
        window.colorCodeStatus = false;
      }
      if (window.captureColorStatus) {
        updatefrom();
        window.captureColorStatus = false;
      }
    }
  }

  function getColorFromRange() {
    let rgbC = Converter.HSBToRGB(hsv_color[0], "100", "100");
    hexRangeColor = "#" + Converter.RGBToHex(rgbC.red, rgbC.green, rgbC.blue);
    ctx.fillStyle = `rgb(${rgbC.red}, ${rgbC.green}, ${rgbC.blue})`;
    ctx.fillRect(0, 0, 200, 300);

    let grdWhite = ctx.createLinearGradient(0, 0, 200, 0);
    grdWhite.addColorStop(0, "rgba(255,255,255,1)");
    grdWhite.addColorStop(1, "rgba(255,255,255,0)");
    ctx.fillStyle = grdWhite;
    ctx.fillRect(0, 0, 200, 300);

    let grdBlack = ctx.createLinearGradient(0, 0, 0, 300);
    grdBlack.addColorStop(0, "rgba(0,0,0,0)");
    grdBlack.addColorStop(1, "rgba(0,0,0,1)");
    ctx.fillStyle = grdBlack;
    ctx.fillRect(0, 0, 200, 300);

    window.colorCodeIsActive = false;
  }
</script>

<div class="container">
  <div class="picker-container">
    <div class="color-picker">
      <div
        data-y={circlePosition.y}
        data-x={circlePosition.x}
        class="circle"
        bind:this={color_selector}
      />
      <canvas
        id={canvas_id}
        class="color-block"
        bind:this={canvasBlock}
        width="200"
        height="300"
      />
    </div>
    <div class="color-range">
      <input
        type="range"
        class="range"
        min="0"
        max="361"
        step="1"
        bind:value={rangeValue}
        style={hue_colors}
      />
    </div>
  </div>
  <div class="alpha-range">
    <input
      type="range"
      class="alphaRange"
      style="background: transparent linear-gradient(90deg, #00000000 0%, {hexRangeColor} 100%) 0% 0% no-repeat padding-box;"
      min="0"
      max="100"
      step="1"
      bind:value={rangeAlphaValue}
    />
    <input
      type="number"
      class="alpha-input"
      value={rangeAlphaValue}
      on:input={getAlphaPosition}
    />
  </div>
</div>

<style>
  .container {
    display: flex;
    flex-direction: column;
  }
  .color-picker {
    width: 200px;
    height: 200px;
    display: flex;
    flex-grow: 1;
  }

  .color-range {
    width: 16px;
    height: 200px;
    margin-left: 12px;
    position: relative;
  }

  input[type="range"] {
    -webkit-appearance: none;
  }

  .alpha-range {
    display: flex;
  }

  .range {
    transform: rotate(-90deg);
    width: 200px;
    height: 16px;
    position: absolute;
    top: 93.2px;
    left: -91px;
    border-bottom: 1px solid #d3d3d3;
    border-top: 1p solid #d3d3d3;
    border-radius: 40px;
  }

  input[type="number"]::-webkit-inner-spin-button {
    -webkit-appearance: none;
  }

  .alpha-input {
    width: 20%;
    height: 32px;
    border: 1px solid #e1e8ed;
    border-radius: 20px;
    font-size: 12px;
    margin-right: 16px;
    transform: translateY(5px);
    text-align: center;
  }

  input[type="range"]::-webkit-slider-thumb {
    -webkit-appearance: none;
    height: 14px;
    width: 14px;
    border-radius: 50%;
    box-shadow: 0 0 2px 0 #555;
    border: 1px solid #ffffff;
  }

  input[type="range"]::-webkit-slider-runnable-track {
    -webkit-appearance: none;
    box-shadow: none;
    border: none;
    background: transparent;
  }

  .color-block {
    width: 200px;
    height: 200px;
    border-radius: 10px;
    position: relative;
  }

  .picker-container {
    display: flex;
    flex-direction: row;
  }

  .alphaRange {
    width: 200px;
    height: 16px;
    margin-top: 16px;
    border-radius: 22px;
  }
  .color-picker {
    position: relative;
  }
  .picker-container {
    overflow: clip;
  }
  .color-picker .circle {
    z-index: 50;
    cursor: pointer !important;
    position: absolute;
    width: 15.84px;
    height: 15.84px;
    border-radius: 50%;
    background: rgba(0, 0, 0, 0.2);
    border: 2px solid #fff;
    top: 0;
    left: 184px;
    touch-action: none;
    user-select: none;
  }
</style>
